<template>
	<view class="xinyi-content">
		<xinyi-nav-bar title="裁决记录" leftIcon="undo-filled" right-text="退出登录"></xinyi-nav-bar>
		<view class="product-list__view">
			<view
				class="product-item"
				v-for="(item, index) in verdictRecordList"
				:key="index"
				@click="nativeTo(item.verdictRecordId)"
			>
				<uni-card spacing="0" padding="10px" margin="10px">
					<image :src="item.coverUrl" class="product-cover__image" mode="aspectFit"></image>
					<view class="product-name__view">
						<view>推演时间：{{ item.createTime }}</view>
						<view>{{ item.firstUserName }}-{{ item.firstCampName }}</view>
						<view>{{ item.secondUserName }}-{{ item.secondCampName }}</view>
					</view>
				</uni-card>
			</view>
		</view>
		<uni-pagination
			class="page"
			:total="total"
			:current="queryParams.pageNum"
			@change="pageChange"
			:pageSize="queryParams.pageSize"
		></uni-pagination>
		<!-- <uni-card margin="10rpx" v-for="(item, index) in VerdictRecordList" :key="index">
			<div>编号：{{ item.verdictRecordId }}</div>
			<div>{{ item.firstUserName }}-{{ item.firstCampName }}</div>
			<div>{{ item.secondUserName }}-{{ item.secondCampName }}</div>
		</uni-card> -->
	</view>
</template>

<script>
import { getOssById } from '@/api/system.js';
import { queryVerdictRecordList } from '../../api/verdictRecord.js';
export default {
	data() {
		return {
			verdictRecordList: [],
			queryParams: {
				pageSize: 10,
				pageNum: 1,
				productId: ''
			},
			total: 0
		};
	},
	onLoad(option) {
		if (option.productId) {
			this.queryParams.productId = option.productId;
		}
		this.queryVerdictRecordListFunc();
	},
	methods: {
		async queryVerdictRecordListFunc() {
			this.verdictRecordList = [];
			let res = await queryVerdictRecordList(this.queryParams);
			console.log('res: ', res);
			this.verdictRecordList = res.data.data.rows;
			this.total = res.data.data.total;
			this.verdictRecordList.forEach(async (item) => {
				if (item.cover) {
					let oss = await getOssById(item.cover);
					let coverUrl = this.baseOssIpPort + oss.data.data.fileName;
					this.$set(item, 'coverUrl', coverUrl);
				}
			});
			// this.VerdictRecordList = rst.result.records;
		},
		nativeTo(val) {
			uni.navigateTo({
				url: '/pages/verdict/detail-v2?id=' + val
			});
		},
		pageChange(e) {
			console.log('e: ', e);
			this.queryParams.pageNum = e.current;
			this.queryVerdictRecordListFunc();
		}
	}
};
</script>

<style lang="scss" scoped>
.product-list__view {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 720rpx;
	.product-item {
		width: 144rpx;
		.product-cover__image {
			width: 100rpx;
			height: 100rpx;
			margin-left: 11rpx;
		}
		.product-name__view {
			text-align: center;
		}
	}
}

.page {
	position: fixed;
	bottom: 10px;
}
</style>
